@import url('../index.css');

.dock {
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-self: center;
    flex-wrap: wrap;
    position: fixed;
    bottom: 10px;
    background: var(--background-color);
    border-radius: 20px;
    padding: 1%;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    z-index: 0;
    border: 2px solid #252344;
  }
  
 
  